.logo-barra {
  padding: 13px 0px 0px 20px;
  width: 220px;
}
.barra {
  padding-top: 12px;
}
.busqueda {
  background: #fff;
  display: flex;
  border: 1px solid #dfe1e5;
  box-shadow: none;
  border-radius: 24px;
  height: 44px;
  margin-top: 35px;
  width: 100%;
}
.busqueda:hover {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
}
input {
  background-color: transparent !important;
  border: none;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.87);
  word-wrap: break-word;
  outline: none;
  display: flex;
  flex: 100%;
  padding: 15px;
  -webkit-tap-highlight-color: transparent;
}
